import React from 'react';
import manufacturing from './manufacturing.png'
import './Third.css';

import {
  
} from 'reactstrap';

const Third = () => {
  return (
    <section className="main-third">
        <div>
            <img src={manufacturing} alt="manufacturing" height="370" width="443" />
        </div>
        <div style={{ backgroundColor:"#FC7000",textAlign:"center",color:"white",padding:"1em" }}>
            <h4>Gagnez de l'argent en voyageant</h4>
            <hr style={{ borderTop:"4px solid white", width:"4em" }} />
            <p style={{ textAlign:"left",color:"white" }}>
                There are many variations of passages of Lorem Ipsum available, <br />
                but the majority have suffered alteration in some form, by injected humour, <br />
                or randomised words which don't look even slightly believable. If you are going <br /> 
                to use a passage of Lorem Ipsum, you need to be sure there isn't anything embarrassing <br />
                hidden in the middle of text. 
            </p>
            <button className="third-section-button">En savoir plus</button>
        </div>
    </section>  
  );
}

export default Third;
