import React from 'react';
import './Second.css';

const Second = () => {
  return (
    <section className="main-second">
        <h4 style={{ color:"#1B3C68" }}>Une solution Efficace</h4>
        <hr style={{ borderTop:"4px solid #1B3C68", width:"4em" }} />
        <div className="second-photo">
            <div className="box-photo photo-1">
                <p>Directe</p>
                <hr style={{ borderTop:"4px solid white", width:"4em" }} />
            </div>
            <div className="box-photo photo-2">
                <p>Securisee</p>
                <hr style={{ borderTop:"4px solid white", width:"4em" }} />
            </div>
            <div className="box-photo photo-3">
                <p>Convivial</p>
                <hr style={{ borderTop:"4px solid white", width:"4em" }} />
            </div>
        </div>
    </section>  
  );
}

export default Second;
