import React, { useState } from 'react';
import logo from './logo.png';
import add from './addcircle.svg';
import search from './search.svg';
import calendar from './calendar.svg';
import './Header.css';


import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink
} from 'reactstrap';

const Header = (props) => {
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);

  return (
    <header className="head-back">
      <Navbar className="header-nav" expand="md">
        <NavbarBrand href="/">
            <img src={logo} alt="logo" height="30px" style={{ marginRight:"0.2em"}} /> 
            <strong className="brand">OuisenD</strong>
        </NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="ml-auto" navbar>
            <NavItem>
              <NavLink href="#"><span style={{ marginRight:"0.1em"}}><img className="nav-button" src={search} alt="search"/></span>Rechercher</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="#"><span style={{ marginRight:"0.1em"}}><img className="nav-button" src={add} alt="add"/></span>Devenir transporteur</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="#">Connexion</NavLink>
            </NavItem>
            <NavItem>
              <NavLink className="insc-link" href="#">Inscription</NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
      <div className="head-text">
        <div className="text">
            <h2>N'attendez plus!</h2> 
            <h2>Envoyez vos colis avec OuisenD!</h2>
            <form className="head-form">
                <input className="head-input" type="text" placeholder="Addresse de depart" />
                <img className="head-calendar" src={calendar} alt="calendar" style={{ marginRight:"0.1em",marginLeft:"0.1em"}}/>
                <input className="head-input" type="text" placeholder="Addresse d'arrivee" />
                <img className="head-calendar" src={calendar} alt="calendar" style={{ marginRight:"0.1em",marginLeft:"0.1em"}}/>
                <input className="head-input" type="text" placeholder="Poids (Kg)" />
                <button className="head-button"><img className="head-button-image" src={search} alt="calendar" /></button>
            </form>
        </div> 
      </div>
    </header>
  );
}

export default Header;
