import React from 'react';
import messenger from './messenger.png';
import './First.css';

const First = () => {
  return (
    <section className="main-first">
        <div style={{ textAlign:"center" }}>
            <h4>Envoie direct de colis</h4>
            <hr style={{ borderTop:"4px solid #1B3C68", width:"4em" }} />
            <p style={{ textAlign:"left",color:"#1B3C68" }}>
                There are many variations of passages of Lorem Ipsum available, <br />
                but the majority have suffered alteration in some form, by injected humour, <br />
                or randomised words which don't look even slightly believable. If you are going <br /> 
                to use a passage of Lorem Ipsum, you need to be sure there isn't anything embarrassing <br />
                hidden in the middle of text. 
            </p>
            <button className="section-button">En savoir plus</button>
        </div>
        <div>
            <img src={messenger} alt="messenger" height="370" width="578" />
        </div>
    </section>  
  );
}

export default First;
